@font-face {
  font-family: 'Phosphate';
  font-style: normal;
  font-weight: normal;
  src: url('./assets/fonts/Phosphate/PhosphateInline.ttf');
}

@font-face {
  font-family: 'SFPro';
  font-style: normal;
  font-weight: normal;
  src: url('./assets/fonts/SFPro/FontsFree-Net-SFProDisplay-Regular.ttf');
}

.currency-flag {
  /* display: inline-block; */
  width: 23px;
  height: 16px;
  margin-left: 10px;
  /* border-radius: 50%; */
  background-size: cover;
  background-image: url('https://wise.com/public-resources/assets/flags/rectangle_fallback.png');
}

.removeArrow .mat-select-arrow {
  display: none !important;
}

.mat-select-value {
  overflow: inherit !important;
}

.currency-flag-sm {
  width: 16px;
  height: 10px;
}

.snackbar {
  margin-top: 15% !important;
}

.currency-flag-lg {
  width: 36px;
  height: 24px;
}

.Nextbtn {
  margin: 12px 0px 12px 0px;
  width: 87px;
  font-size: 8px !important;
  line-height: 31px !important;
  color: white;
  border-radius: 55px !important;
}

legend {
  width: auto;
  font-size: 8px;
  text-align: center;
  padding-left: 18px;
  padding-right: 15px;
  color: grey;
  /* float: right; */
}

fieldset {
  border-top: 1px solid lightblue;
  border-bottom: 0px solid lightblue;
  border-right: 0px solid lightblue;
  border-left: 0px solid lightblue;
  margin-right: 60%;

}

.egret-navy .mat-select-value {
  font-weight: bold !important;
  color: #102245 !important;
}

.currency-flag-xl {
  width: 48px;
  height: 32px;
}

/*TOOLTIP  START*/

.tooltip {
  position: relative;
  display: flex;
  margin-top: 0px;
  margin-left: -26px;
}

.tooltip>.text {
  visibility: hidden;
  position: absolute;
}

.tooltip .text>p {
  white-space: nowrap !important;
}

.tooltip .tooltiptext {
  width: 1305%;
  background: transparent linear-gradient(91deg, #051A2D 0%, #004C97 100%) 0% 0% no-repeat padding-box !important;
  color: #fff;
  text-align: start;
  border-radius: 6px;
  padding: 5px 15px;
  margin-left: 5px;
  margin-bottom: 50px;
  display: inline-block;
  transform: translateX(7%);
  z-index: 1;
}

.tooltip .tooltiptext::after {
  content: "";
  position: absolute;
  border-radius: 2px;
  top: 7%;
  right: 99%;
  border-width: 10px;
  border-style: solid;
  border-color: transparent #051A2D transparent transparent;
  transform: rotate(0deg);
}


.tooltip .tooltiptextNeg {
  width: 260px;
  max-width: 300px;
  background: transparent linear-gradient(91deg, #051A2D 0%, #004C97 100%) 0% 0% no-repeat padding-box !important;
  color: #fff;
  text-align: start;
  border-radius: 6px;
  padding: 5px 15px;
  display: inline-block;
  z-index: 1;
  transform: translateX(-104%);
  top: -10px;
}

.text-center {
  text-align: center;
}

.tooltip .tooltiptextNeg::after {
  content: "";
  position: absolute;
  border-radius: 2px;
  top: 12%;
  right: -6.6%;
  margin-top: -5px;
  border-width: 10px;
  border-style: solid;
  border-color: transparent #004C97 transparent transparent;
  transform: rotate(180deg);
}



.tooltip:hover .tooltiptext {
  visibility: visible;
  top: -45%;
}

.tooltip:hover .tooltiptextNeg {
  visibility: visible;
}

/* TOOLTIP END */

.screensize {
  display: block;
}

.mat-card {
  overflow: visible !important;
}
input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus, 
input:-webkit-autofill:active  {
    -webkit-box-shadow: 0 0 0 40px  white inset !important;
}

